<style lang="scss" scope>
.item_reset{
    width: 180px;
}
</style>
<template>
    <el-dialog :title="title" :visible.sync="addDialogVisible" width="40%">
        <div class="ui-box leave_dialog">
            <el-form :model="departmentItem" label-width="80px" :rules="formRule" ref="formPerms">
                <el-form-item label="名称" prop="name">
                    <el-input v-model="departmentItem.name" size="mini" class="item_reset"></el-input>
                </el-form-item>
                <el-form-item label="说明">
                    <el-input v-model="departmentItem.description" class="item_reset" size="mini"></el-input>
                </el-form-item>
                <!-- <el-form-item label="状态">
                    <el-select placeholder="请选择" class="item_reset" size="mini" v-model="departmentItem.status">
                        <el-option label="禁用" value="2"></el-option>
                        <el-option label="启用" value="1"></el-option>
                    </el-select>
                </el-form-item> -->
            </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false" size="mini">取 消</el-button>
            <el-button type="primary" @click="submit" size="mini">确 定</el-button>
        </span>
    </el-dialog>
</template>
<script>
export default {
    name: 'addItem',
    props: {
        departmentItem:{}
    },
    data(){
        return{
            formRule:{
                name: [{required:true,message:'请输入名称',trigger: 'blur'}],
            },
            addDialogVisible: false,
            title: '',
            parentRoute:[],
        }
    },
    mounted(){
        this.initParentRoute()
    },
    methods:{
        submit(){
            this.$refs['formPerms'].validate((val) =>{
                if(val){
                    this.$emit('submit')
                }
            })
        },
        openWindow(value){
            // console.log('title',value);
            this.title=value;
            this.addDialogVisible=true;
        },
        initParentRoute(){
            this.parentRoute=this.$getGlobal.getItem('routers')

        }
    }
}
</script>




