<style lang="scss" scoped>

</style>
<template>
  <page-box>
        <div class="ui-search-box" slot="search-form">
            <el-form class="ui-search-form"  label-width="120px">
                <el-form-item label="部门id">
                    <el-input size="mini" class="ui-item-reset" v-model="params.departmentId" clearable></el-input>
                </el-form-item>
                <el-form-item label="名称">
                    <el-input size="mini" class="ui-item-reset" v-model="params.name" clearable></el-input>
                </el-form-item>
                <el-form-item label="说明">
                    <el-input size="mini" class="ui-item-reset" v-model="params.description" clearable></el-input>
                </el-form-item>
            </el-form>
            <div class="ui-search-btus">
                <el-button type="primary" size="mini" @click="getData">搜索</el-button>
                <!-- <el-button size="mini">重置</el-button> -->
            </div>
        </div>
        <div class="ui-list ui-box" slot="list-box">
            <div class="box-header ui-list-header">
                <div>部门列表</div>
                <div>
                    <el-button type="primary" size="mini" @click="exportExcel">导出</el-button>
                    <el-button type="primary" size="mini" @click="open">新增</el-button>
                </div>
            </div>
            <el-table v-loading="tabLoading" :data="dataList" @selection-change="handleSelectionChange"  :cell-style="cellStyle" :header-cell-style="rowClass">
                <!-- <el-table-column type="selection" width="55"></el-table-column> -->
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-table :data="props.row.userList" style="color:#AE3939;" v-if="props.row.userList.length>0" @selection-change="handleSelectionChange" :cell-style="cellStyle" :header-cell-style="rowClass">
                            <el-table-column prop="userId" label="用户id"></el-table-column>
                            <el-table-column prop="username" label="用户名"></el-table-column>
                            <el-table-column prop="email" label="邮箱"></el-table-column>
                            <el-table-column prop="phone" label="手机号"></el-table-column>
                            <el-table-column prop="sex" label="性别">
                                <template slot-scope="scope">
                                    <span v-if="scope.row.sex==1">男</span>
                                    <span v-else>女</span>
                                </template>
                            </el-table-column>               
                            <el-table-column prop="status" label="状态">
                                <template slot-scope="scope">
                                    <el-button type="success" icon="el-icon-check" circle size="mini" v-if="scope.row.status==1"></el-button>
                                    <el-button type="danger" icon="el-icon-close" circle v-else size="mini"></el-button>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="150px">
                                <template slot-scope="scope">
                                        <el-button size="mini" @click="checkUserDetail(scope.row)">详情</el-button>
                                        <el-button size="mini" type="danger" @click="deleteUserByDepartment(scope.row,props.$index)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div v-else  style="color:#AE3939;">
                            对不起,{{props.row.name}}暂时没有员工
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="departmentId" label="部门id"></el-table-column>
                <el-table-column prop="name" label="部门名称"></el-table-column>
                <el-table-column prop="description" label="说明"></el-table-column>             
                <el-table-column prop="status" label="状态">
                    <template slot-scope="scope">
                        <el-button type="success" icon="el-icon-check" circle size="mini" v-if="scope.row.status==1"></el-button>
                        <el-button type="danger" icon="el-icon-close" circle v-else size="mini"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="createTime" label="创建时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="updateTime" label="更新时间" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作" width="250px">
                    <template slot-scope="scope">
                            <el-button size="mini" @click="openEdit(scope.row)">编辑</el-button>
                            <el-button size="mini" @click="openDialog(scope.row)">增加用户</el-button>
                            <el-button size="mini" type="danger" @click="deleteItem(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-dialog title="请选择要添加的员工" :visible.sync="addUserFlag">
                <el-select v-model="addUserItem" placeholder="请选择" size="mini">
                    <el-option :label="item.username" :value="item.userId" v-for="item in userList" :key="item.userId" :disabled="item.select==1"></el-option>
                </el-select>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="addUserFlag = false" size="mini">取 消</el-button>
                    <el-button type="primary" @click="addUserToDepart" size="mini">确 定</el-button>
                </div>
            </el-dialog>
            <el-dialog title="编辑部门" :visible.sync="editDepartFlag" width="400px">
                <el-form :model="departTmpItem" label-width="80px" ref="formPerms">
                    <el-form-item label="名称" required>
                        <el-input v-model="departTmpItem.name" size="mini" class="item_reset"></el-input>
                    </el-form-item>
                    <el-form-item label="说明">
                        <el-input v-model="departTmpItem.description" class="item_reset" size="mini"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="editDepartFlag = false" size="mini">取 消</el-button>
                    <el-button type="primary" @click="editItem" size="mini">确 定</el-button>
                </div>
            </el-dialog>
            <add-item v-bind:departmentItem.sync='departmentItem' @submit="addItem" ref="addPerm"></add-item>
        </div>
  </page-box>
</template>
<script>
import pageBox from '@/components/pageBox'
import addItem from '@/components/department/addItem.vue'
export default {
    name: 'departmentList',
    components:{
        pageBox,addItem
    },
    data(){
        return{
            dataList: [],
            exportList: [],
            departmentItem:{
                name: '',
                description: '',
                // status: '1',
            },
            userList: [],
            params:{
                departmentId:'',
                name: '',
                description: '',
            },
            addUserItem:'',
            tabLoading: false,
            addUserFlag: false,
            editDepartFlag: false,
            departTmpItem:{}, // 暂时性的存储一个部门，用于为部门新增用户时
        }
    },
    created(){
        this.getData()
    },
    methods:{
        getData(){
            this.tabLoading=true
            post('/pms_api/department/list',this.params).then(res => {
                this.dataList=res.rows
                this.getUserByDepartment()
                
            }).catch(err => {
                console.log(err)
                this.tabLoading=false
            })
        },
        addItem(){
            post('/pms_api/department/add',this.departmentItem).then(res => {
                this.$refs.addPerm.addDialogVisible=false;
                this.getData();
            }).catch(err => {
                console.log(err)
            })
            this.$refs.addPerm.addDialogVisible=false;
        },
        open(){
            // this.addDialogVisible=!this.addDialogVisible
            this.$refs.addPerm.openWindow('新增部门')
        },
        openEdit(item){
            this.departTmpItem=item
            this.editDepartFlag=true
        },
        editItem(){
            post('/pms_api/department/edit',this.departTmpItem).then(res => {
                this.editDepartFlag=true
                this.getData();
            }).catch(err => {
                console.log(err)
            })
        },
        deleteItem(item){
            post('/pms_api/department/delete',{'departmentId':item.departmentId}).then(res => {
                this.getData()
            }).catch(err => {
                console.log(err)
            })
        },
        // 用户部门联动操作
        checkUserDetail(item){
            this.$router.push({
                path: '/userDetail',
                query:{
                    title: item.name,
                    id: item.userId
                }
            })
        },
        openDialog(item){
            this.departTmpItem=item
            this.getUsers()
            this.addUserFlag=true
        },
        getUsers(){
            post('/pms_api/user/list').then(res => {
                this.userList=res.rows
                for(let i in this.dataList){
                    for(let j in this.dataList[i].userList){
                        for(let k in this.userList){
                            if(this.userList[k].userId==this.dataList[i].userList[j].userId){
                                this.userList[k].select='1'
                            }
                        }
                    }
                }
                
                
            }).catch(err => {
                console.log(err)
            })
        },
        addUserToDepart(){ //增加员工到部门,参数是当前部门
            let userStr=[]
            for(let i in this.departTmpItem.userList){
                userStr.push(this.departTmpItem.userList[i].userId)
            }
            userStr.push(this.addUserItem)
            post('/pms_api/department/assign/department',{'departmentId':this.departTmpItem.departmentId,'userIdStr':userStr}).then(res => {
                this.addUserFlag=false
                this.getData();
            }).catch(err => {
                console.log(err)
            })
        },
        getUserByDepartment(){
            for(let i in this.dataList){
                post('/pms_api/department/assign/department/list/',{'departmentId':this.dataList[i].departmentId}).then(res => {
                    this.dataList[i].userList=res.rows
                }).catch(err => {
                    console.log(err)
                })
            }
            this.tabLoading=false
        },
        deleteUserByDepartment(item,departmentIndex){
            let userStr=[]
            for(let i in this.dataList[departmentIndex].userList){
                if(this.dataList[departmentIndex].userList[i].userId!=item.userId){
                    userStr.push(this.dataList[departmentIndex].userList[i].userId)
                }
            }
            post('/pms_api/department/assign/department',{'departmentId':this.dataList[departmentIndex].departmentId,'userIdStr':userStr}).then(res => {
                this.getData()
            }).catch(err => {
                console.log(err)
            })
        },
        handleSelectionChange(val){
            this.exportList=val
        },
        exportExcel(){
           // 暂时无法完成 xlsx
           this.$message.warning(`抱歉，无法导出`)
        },
        //表格居中
        cellStyle({row, colum, rowIndex, columIndex}){
            return "text-align: center";
        },
        rowClass({row, rowIndex}){
            return "text-align: center";
        }
    }
}
</script>





